.circle {
  position: absolute;
  z-index: 10;
  height: 20px;
  width: 20px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-image: linear-gradient(-45deg, #a68c03, #ffd805 45%);
  border-radius: 50%;
  -webkit-animation: circle 2s ease-in-out;
  animation: circle 2s ease-in-out;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.line {
  position: absolute;
  z-index: 10;
  width: 150px;
  height: 1px;
  top: 198px;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
}

.line.left {
  left: 50px;
  -webkit-transform-origin: 100% 50%;
  transform-origin: 100% 50%;
  -webkit-animation: lines 1s ease-in-out 0.8s, line-left 1s steps(1) 0.8s;
  animation: lines 1s ease-in-out 0.8s, line-left 1s steps(1) 0.8s;
  background: rgba(255, 255, 255, 0.7);
}

.line.right {
  right: 50px;
  background: rgba(255, 255, 255, 0.7);
  -webkit-transform-origin: 0% 50%;
  transform-origin: 0% 50%;
  -webkit-animation: lines 1s ease-in-out 0.8s, line-right 1s steps(1) 0.8s;
  animation: lines 1s ease-in-out 0.8s, line-right 1s steps(1) 0.8s;
}

.bracket {
  position: absolute;
  z-index: 10;
  height: 130px;
  width: 1px;
  top: 137px;
  background: rgba(255, 255, 255, 0.7);
  -webkit-animation: bracket 0.4s ease-out 1.7s;
  animation: bracket 0.4s ease-out 1.7s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.bracket:before,
.bracket:after {
  position: absolute;
  display: block;
  content: "";
  width: 25px;
  height: 1px;
}

.bracket.right:before,
.bracket.right:after {
  background: rgba(255, 255, 255, 0.7);
}

.bracket.left {
  left: 50px;
}

.bracket.left:before {
  top: 0;
  left: 0;
  -webkit-transform-origin: 0% 50%;
  transform-origin: 0% 50%;
  -webkit-animation: bracket-line 0.2s ease-out 2.1s;
  animation: bracket-line 0.2s ease-out 2.1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.bracket.left:after {
  bottom: 0;
  left: 0;
  -webkit-transform-origin: 0% 50%;
  transform-origin: 0% 50%;
  -webkit-animation: bracket-line 0.2s ease-out 2.1s;
  animation: bracket-line 0.2s ease-out 2.1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.bracket.left:before,
.bracket.left:after {
  background: rgba(255, 255, 255, 0.7);
}

.bracket.right {
  right: 50px;
}

.bracket.right:before {
  top: 0;
  right: 0;
  -webkit-transform-origin: 100% 50%;
  transform-origin: 100% 50%;
  -webkit-animation: bracket-line 0.2s ease-out 2.1s;
  animation: bracket-line 0.2s ease-out 2.1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.bracket.right:after {
  bottom: 0;
  right: 0;
  -webkit-transform-origin: 100% 50%;
