/* box sliding animation */

:root {
  --duration: 1.5s;
  --container-size: 250px;
  --box-size: 33px;
  --box-border-radius: 15%;
}

.coming-soon-loading-animation {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
}

.boxAnimation-container {
  width: 250px;
  width: var(--container-size);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -webkit-align-items: center;
  align-items: center;
  position: relative;
}

. {
  width: 33px;
  width: var(--box-size);
  height: 33px;
  height: var(--box-size);
  position: relative;
  display: block;
  -webkit-transform-origin: -50% center;
  transform-origin: -50% center;
  border-radius: 15%;
  border-radius: var(--box-border-radius);
}

.:after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  background-color: lightblue;
  border-radius: 15%;
  border-radius: var(--box-border-radius);
  box-shadow: 0px 0px 10px 0px rgba(236, 210, 61, 0.4);
}

.:nth-child(1) {
  -webkit-animation: slide 1.5s ease-in-out infinite alternate;
  animation: slide 1.5s ease-in-out infinite alternate;
  -webkit-animation: slide var(--duration) ease-in-out infinite alternate;
  animation: slide var(--duration) ease-in-out infinite alternate;
}

.:nth-child(1):after {
  -webkit-animation: color-change 1.5s ease-in-out infinite alternate;
  animation: color-change 1.5s ease-in-out infinite alternate;
  -webkit-animation: color-change var(--duration) ease-in-out infinite alternate;
  animation: color-change var(--duration) ease-in-out infinite alternate;
}

.:nth-child(2) {
  -webkit-animation: flip-1 1.5s ease-in-out infinite alternate;
  animation: flip-1 1.5s ease-in-out infinite alternate;
  -webkit-animation: flip-1 var(--duration) ease-in-out infinite alternate;
  animation: flip-1 var(--duration) ease-in-out infinite alternate;
}

